<template>
    <!--
    	luoyiming
    	2019-10-26
    	--/--
    -->
    <div class="mt16">
        <el-form-item label="" v-if="form.model.spec_many && form.model.spec_many.spec_list.length > 0">
            <div>
                
                <!-- <el-input size="small" v-model="batchData.product_price" placeholder="" style="width: 160px;padding-left: 4px;"></el-input>
                <el-input size="small" v-model="batchData.line_price" placeholder="" style="width: 160px;padding-left: 4px;"></el-input> -->
                <el-input size="small" v-model="batchData.love_price" placeholder="" style="width: 160px;padding-left: 4px;"></el-input>
                <el-input size="small" v-model="batchData.beans_price" placeholder="" style="width: 160px;padding-left: 4px;"></el-input>
                <el-input size="small" v-model="batchData.stock_num" placeholder="" style="width: 160px;padding-left: 4px;"></el-input>
                <el-input size="small" v-model="batchData.product_weight" placeholder="" style="width: 160px;padding-left: 4px;"></el-input>
                <el-button size="small" @click="onSubmitBatchData"></el-button>
            </div>
            <!---->
            <div>
                <el-table
                    size="mini"
                    :data="form.model.spec_many.spec_list"
                    :span-method="objectSpanMethod"
                    border
                    style="width: 100%; margin-top: 20px"
                >
                    <el-table-column :label="item.group_name" :key="item.group_name" v-for="(item, index) in form.model.spec_many.spec_attr">
                        <template slot-scope="scope">
                            {{ scope.row.rows[index].spec_value }}
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <img v-img-url="scope.row.spec_form.image_path" alt="" width="35" height="35" @click="chooseSpecImage(scope.$index)" />
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-form-item label="" style="margin-bottom: 0;"
                                ><el-input size="small" prop="product_no" v-model="scope.row.spec_form.product_no"></el-input
                            ></el-form-item>
                        </template>
                    </el-table-column>
                    <!-- <el-table-column label="">
                        <template slot-scope="scope">
                            <el-form-item
                                label=""
                                :rules="[{ required: true, message: ' ' }]"
                                :prop="'model.spec_many.spec_list.' + scope.$index + '.spec_form.product_price'"
                                style="margin-bottom: 0;"
                            >
                                <el-input size="small" prop="product_price" v-model="scope.row.spec_form.product_price"></el-input>
                            </el-form-item>
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-form-item label="" style="margin-bottom: 0;"
                                ><el-input size="small" prop="line_price" v-model="scope.row.spec_form.line_price"></el-input
                            ></el-form-item>
                        </template>
                    </el-table-column> -->
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-form-item
                                label=""
                                :rules="[{ required: true, message: ' ' }]"
                                :prop="'model.spec_many.spec_list.' + scope.$index + '.spec_form.love_price'"
                                style="margin-bottom: 0;"
                            >
                                <el-input size="small" prop="love_price" v-model="scope.row.spec_form.love_price"></el-input>
                            </el-form-item>
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-form-item
                                label=""
                                :rules="[{ required: true, message: ' ' }]"
                                :prop="'model.spec_many.spec_list.' + scope.$index + '.spec_form.beans_price'"
                                style="margin-bottom: 0;"
                            >
                                <el-input size="small" prop="beans_price" v-model="scope.row.spec_form.beans_price"></el-input>
                            </el-form-item>
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-form-item
                                label=""
                                :rules="[{ required: true, message: ' ' }]"
                                :prop="'model.spec_many.spec_list.' + scope.$index + '.spec_form.stock_num'"
                                style="margin-bottom: 0;"
                            >
                                <el-input size="small" prop="stock_num" v-model="scope.row.spec_form.stock_num"></el-input>
                            </el-form-item>
                        </template>
                    </el-table-column>
                    <el-table-column label="(kg)">
                        <template slot-scope="scope">
                            <el-form-item
                                label=""
                                :rules="[{ required: true, message: ' ' }]"
                                :prop="'model.spec_many.spec_list.' + scope.$index + '.spec_form.product_weight'"
                                style="margin-bottom: 0;"
                            >
                                <el-input size="small" prop="product_weight" v-model="scope.row.spec_form.product_weight"></el-input>
                            </el-form-item>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-form-item>
        <!---->
        <Upload v-if="isupload" :isupload="isupload" @returnImgs="returnImgsFunc"></Upload>
    </div>
</template>

<script>
import PorductApi from '@/api/product.js'
import Upload from '@/components/file/Upload'
export default {
    components: {
        Upload,
    },
    data() {
        return {
            /*sku*/
            batchData: {
                supplier_price: '',
                line_price: '',
                stock_num: '',
                product_weight: '',
                love_price: '',
                beans_price: '',
            },
            /**/
            isupload: false,
            //
            spec_index: -1,
        }
    },
    inject: ['form'],
    created() {},
    methods: {
        /**/
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
            var spec_attr = this.form.model.spec_many.spec_attr
            //  (table)
            var totalRow = 1
            //2,
            if (columnIndex < spec_attr.length - 1) {
                let startRowIndex = columnIndex + 1
                let endRowIndex = spec_attr.length - 1
                for (var i = startRowIndex; i <= endRowIndex; i++) {
                    totalRow *= spec_attr[i].spec_items.length
                }
                if (rowIndex % totalRow === 0) {
                    return {
                        rowspan: totalRow,
                        colspan: 1,
                    }
                } else {
                    return {
                        rowspan: 0,
                        colspan: 0,
                    }
                }
            }
        },

        /*sku*/
        onSubmitBatchData: function() {
            var self = this
            self.form.model.spec_many.spec_list.forEach(function(value) {
                for (var key in self.batchData) {
                    if (self.batchData.hasOwnProperty(key) && self.batchData[key]) {
                        self.$set(value.spec_form, key, self.batchData[key])
                    }
                }
            })
        },

        /**/
        chooseSpecImage: function(spec_index) {
            this.isupload = true
            this.spec_index = spec_index
        },

        /**/
        returnImgsFunc(e) {
            this.isupload = false
            this.$set(this.form.model.spec_many.spec_list[this.spec_index].spec_form, 'image_id', e[0]['file_id'])
            this.$set(this.form.model.spec_many.spec_list[this.spec_index].spec_form, 'image_path', e[0]['file_path'])
        },
    },
}
</script>

<style></style>



