<template>
    <!--
      luoyiming
      2019-10-25
      
  -->
    <div class="user">
        <!---->
        <div class="common-seach-wrap">
            <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
                <el-form-item label="">
                    <el-input size="small" v-model="searchForm.order_no" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select size="small" v-model="searchForm.style_id" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option v-for="(item, index) in exStyle" :key="index" :label="item.name" :value="item.value"> </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select size="small" v-model="searchForm.store_id" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option v-for="(item, index) in shopList" :key="index" :label="item.store_name" :value="item.store_id"> </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <div class="block">
                        <span class="demonstration"></span>
                        <el-date-picker
                            size="small"
                            v-model="searchForm.create_time"
                            type="daterange"
                            value-format="yyyy-MM-dd"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                        ></el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item>
                    <el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button size="small" type="success" @click="onExport" v-auth="'/order/order/operate/export'"> </el-button>
                </el-form-item>
                <el-form-item style="float: right;">
                    <el-button size="small" type="warning" @click="showBatchDelivery" v-auth="'/order/order/operate/batchDelivery'"
                        ></el-button
                    >
                </el-form-item>
            </el-form>
        </div>
        <!---->
        <div class="product-content">
            <div class="table-wrap">
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="" name="all"></el-tab-pane>
                    <el-tab-pane :label="''" name="payment">
                        <span slot="label"
                            > <el-tag size="mini">{{ order_count.payment }}</el-tag></span
                        >
                    </el-tab-pane>
                    <el-tab-pane :label="''" name="delivery">
                        <span slot="label"
                            > <el-tag size="mini">{{ order_count.delivery }}</el-tag></span
                        >
                    </el-tab-pane>
                    <el-tab-pane :label="''" name="received">
                        <span slot="label"
                            > <el-tag size="mini">{{ order_count.received }}</el-tag></span
                        >
                    </el-tab-pane>
                    <el-tab-pane :label="''" name="cancel">
                        <span slot="label"
                            > <el-tag size="mini">{{ order_count.cancel }}</el-tag></span
                        >
                    </el-tab-pane>
                    <el-tab-pane label="" name="comment"></el-tab-pane>
                    <el-tab-pane label="" name="complete"></el-tab-pane>
                </el-tabs>
                <el-table size="small" :data="tableData.data" :span-method="arraySpanMethod" border style="width: 100%" v-loading="loading">
                    <el-table-column prop="order_no" label="" width="400">
                        <template slot-scope="scope">
                            <div class="order-code" v-if="scope.row.is_top_row">
                                <span class="state-text" :class="{ 'state-text-red': scope.row.order_source != 10 }">{{
                                    scope.row.order_source_text
                                }}</span>
                                <span class="c_main">{{ scope.row.order_no }}</span>
                                <span class="pl16">{{ scope.row.create_time }}</span>
                                <!---->
                                <span class="pl16" v-if="scope.row.order_status == 21">
                                    <el-tag effect="dark" size="mini"></el-tag>
                                </span>
                            </div>
                            <template v-else>
                                <div class="product-info" v-for="(item, index) in scope.row.product" :key="index">
                                    <div class="pic"><img v-img-url="item.image ? item.image.file_path : ''" alt="" /></div>
                                    <div class="info">
                                        <div class="name gray3 product-name">
                                            <span>{{ item.product_name }}</span>
                                        </div>
                                        <div class="gray9" v-if="item.product_attr">{{ item.product_attr }}</div>
                                        <div class="orange" v-if="item.refund">{{ item.refund.type.text }}-{{ item.refund.status.text }}</div>
                                    </div>
                                    <div class="d-c-c d-c">
                                        <div class="orange"> {{ item.total_pay_price }}</div>
                                        <div class="gray3">x{{ item.total_num }}</div>
                                    </div>
                                </div>
                            </template>
                        </template>
                    </el-table-column>
                    <el-table-column prop="pay_price" label="">
                        <template slot-scope="scope" v-if="!scope.row.is_top_row">
                            <div class="orange">{{ scope.row.pay_price }}</div>
                            <p class="gray9">({{ scope.row.express_price }})</p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="" label="">
                        <template slot-scope="scope" v-if="!scope.row.is_top_row">
                            <div>{{ scope.row.user.nickName }}</div>
                            <div class="gray9">ID({{ scope.row.user.game_user_id }})</div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="state_text" label="">
                        <template slot-scope="scope" v-if="!scope.row.is_top_row">
                            {{ scope.row.state_text }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="pay_type.text" label="">
                        <template slot-scope="scope" v-if="!scope.row.is_top_row">
                            <span class="gray9">{{ scope.row.pay_type.text }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="delivery_type.text" label="">
                        <template slot-scope="scope" v-if="!scope.row.is_top_row">
                            <span class="green">{{ scope.row.delivery_type.text }}</span>
                            <span v-if="scope.row.delivery_type.value == 30">:{{ scope.row.user.mobile }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <span v-if="scope.row.is_settled == 0"></span>
                            <span v-if="scope.row.is_settled == 1" class="green"></span>
                        </template>
                    </el-table-column>
                    <el-table-column fixed="right" label="" width="210">
                        <template slot-scope="scope" v-if="!scope.row.is_top_row">
                            <el-button @click="addClick(scope.row)" type="text" size="small" v-auth="'/order/order/detail'"> </el-button>
                            <el-button
                                v-if="scope.row.delivery_type.value == 10 && scope.row.delivery_status.value == 20"
                                @click="getLogistics(scope.row)"
                                type="text"
                                size="small"
                                v-auth="'/order/order/detail'"
                                >
                            </el-button>
                            <el-button
                                v-if="
                                    scope.row.pay_status.value == 20 &&
                                        scope.row.delivery_status.value == 10 &&
                                        scope.row.order_status.value != 20 &&
                                        scope.row.order_status.value != 21 &&
                                        scope.row.delivery_type.value == 10
                                "
                                @click="addClick(scope.row)"
                                type="text"
                                size="small"
                                v-auth="'/order/order/delivery'"
                            >
                                
                            </el-button>
                            <el-button
                                v-if="
                                    scope.row.pay_status.value == 20 &&
                                        scope.row.delivery_status.value == 10 &&
                                        scope.row.order_status.value != 20 &&
                                        scope.row.order_status.value != 21 &&
                                        scope.row.delivery_type.value == 20
                                "
                                @click="addClick(scope.row)"
                                type="text"
                                size="small"
                                v-auth="'/order/order/operate/extract'"
                            >
                                
                            </el-button>
                            <el-button
                                v-if="
                                    scope.row.pay_status.value == 20 &&
                                        scope.row.delivery_status.value == 10 &&
                                        scope.row.order_status.value != 20 &&
                                        scope.row.order_status.value != 21 &&
                                        scope.row.delivery_type.value == 30
                                "
                                @click="addClick(scope.row)"
                                type="text"
                                size="small"
                                v-auth="'/order/order/delivery'"
                            >
                                
                            </el-button>
                            <el-button
                                v-if="scope.row.pay_status.value == 20 && scope.row.delivery_status.value == 10 && scope.row.order_status.value == 10"
                                @click="cancelClick(scope.row)"
                                type="text"
                                size="small"
                                v-auth="'/order/order/operate/confirmCancel'"
                            >
                                
                            </el-button>
                            <el-button
                                v-if="scope.row.order_status.value == 21"
                                @click="addClick(scope.row)"
                                type="text"
                                size="small"
                                v-auth="'/order/order/operate/confirmCancel'"
                                ></el-button
                            >
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <!---->
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="curPage"
                    :page-size="pageSize"
                    layout="total, prev, pager, next, jumper"
                    :total="totalDataNumber"
                ></el-pagination>
            </div>
        </div>
        <Logistics :logisticsData="logisticsData" :isLogistics="isLogistics" @closeDialog="closeLogistics"></Logistics>
        <el-dialog title="" :visible.sync="dialogBatchDelivery" width="30%">
            <div>1</div>
            <div>2excelexcel</div>
            <div>3<el-link type="primary" :underline="false" @click="gotoExpress()"></el-link></div>
            <div style="padding-top: 20px;padding-bottom: 20px;text-align: center;">
                <el-upload
                    class="avatar-uploader"
                    ref="upload"
                    action="string"
                    accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                    :before-upload="onBeforeUploadImage"
                    :http-request="UploadImage"
                    :show-file-list="false"
                >
                    <el-button size="small" icon="el-icon-upload" type="primary"></el-button>
                </el-upload>
            </div>
        </el-dialog>
        <!---->
        <Cancel v-if="open_edit" :open_edit="open_edit" :order_no="order_no" @closeDialog="closeDialogFunc($event, 'edit')"> </Cancel>
    </div>
</template>

<script>
import OrderApi from '@/api/order.js'
import Logistics from '@/components/logistics/viewLogistics.vue'
import qs from 'qs'
import Cancel from './dialog/cancel.vue'
export default {
    components: {
        Cancel,
        Logistics,
    },
    data() {
        return {
            /**/
            activeName: 'all',
            /**/
            loading: true,
            /**/
            tableData: [],
            /**/
            pageSize: 20,
            /**/
            totalDataNumber: 0,
            /**/
            curPage: 1,
            /**/
            searchForm: {
                order_no: '',
                style_id: '',
                store_id: '',
                create_time: '',
            },
            /**/
            exStyle: [],
            /**/
            shopList: [],
            /**/
            create_time: '',
            /**/
            order_count: {
                payment: 0,
                delivery: 0,
                received: 0,
                cancel: 0,
            },
            isLogistics: false,
            logisticsData: {},
            dialogBatchDelivery: false,
            /**/
            open_edit: false,
            /**/
            order_no: 0,
        }
    },
    created() {
        /**/
        this.getData()
    },
    methods: {
        /**/
        arraySpanMethod(row) {
            if (row.rowIndex % 2 == 0) {
                if (row.columnIndex === 0) {
                    return [1, 8]
                }
            }
        },
        /**/
        handleCurrentChange(val) {
            let self = this
            self.curPage = val
            self.getData()
        },

        /**/
        handleSizeChange(val) {
            this.curPage = 1
            this.pageSize = val
            this.getData()
        },

        /**/
        handleClick(tab, event) {
            let self = this
            self.curPage = 1
            self.getData()
        },

        /**/
        getData() {
            let self = this
            let Params = this.searchForm
            Params.dataType = self.activeName
            Params.page = self.curPage
            Params.list_rows = self.pageSize
            self.loading = true
            OrderApi.orderlist(Params, true)
                .then((res) => {
                    let list = []
                    for (let i = 0; i < res.data.list.data.length; i++) {
                        let item = res.data.list.data[i]
                        let topitem = {
                            order_no: item.order_no,
                            create_time: item.create_time,
                            order_source: item.order_source,
                            order_source_text: item.order_source_text,
                            order_status: item.order_status.value,
                            is_top_row: true,
                        }
                        list.push(topitem)
                        list.push(item)
                    }
                    self.tableData.data = list
                    self.totalDataNumber = res.data.list.total
                    self.exStyle = res.data.ex_style
                    self.shopList = res.data.shop_list
                    self.order_count = res.data.order_count.order_count
                    self.loading = false
                })
                .catch((error) => {})
        },
        /**/
        cancelClick(item) {
            this.order_no = item.order_no
            this.open_edit = true
        },
        /**/
        closeDialogFunc(e, f) {
            if (f == 'edit') {
                this.open_edit = e.openDialog
                if (e.type == 'success') {
                    this.getData()
                }
            }
        },
        /**/
        addClick(row) {
            let self = this
            let params = row.order_id
            self.$router.push({
                path: '/order/order/Detail',
                query: {
                    order_id: params,
                },
            })
        },
        getLogistics(row) {
            let self = this
            let Params = {
                order_id: row.order_id,
            }
            self.loading = true
            OrderApi.queryLogistics(Params, true)
                .then((res) => {
                    self.loading = false
                    self.logisticsData = res.data.express.list
                    console.log(self.logisticsData)
                    self.openLogistics()
                })
                .catch((error) => {
                    self.loading = false
                })
        },
        openLogistics() {
            this.isLogistics = true
        },
        closeLogistics() {
            this.isLogistics = false
        },
        /**/
        onSubmit() {
            this.curPage = 1
            this.tableData = []
            this.getData()
        },
        onExport: function() {
            let baseUrl = window.location.protocol + '//' + window.location.host
            window.location.href = baseUrl + '/index.php/supplier/order.operate/export?' + qs.stringify(this.searchForm)
        },
        // 
        showBatchDelivery() {
            this.dialogBatchDelivery = true
        },
        /**/
        gotoExpress() {
            let self = this
            self.$router.push('/setting/express/index')
        },

        /**/
        onBeforeUploadImage(file) {
            var fileType = file.name.substring(file.name.lastIndexOf('.') + 1)
            const isEXCEL = fileType === 'xlsx'
            const isLt10M = file.size / 1024 / 1024 < 10
            if (!isEXCEL) {
                this.$message.error('excel!')
            }
            if (!isLt10M) {
                this.$message.error(' 10MB!')
            }
            return isEXCEL && isLt10M
        },

        /**/
        UploadImage(param) {
            let self = this
            const loading = this.$loading({
                lock: true,
                text: ',',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)',
            })
            const formData = new FormData()
            formData.append('iFile', param.file)
            OrderApi.batchDelivery(formData)
                .then((response) => {
                    loading.close()
                    self.dialogBatchDelivery = false
                    self.$message({
                        message: response.msg,
                        type: 'warning',
                    })
                })
                .catch((response) => {
                    loading.close()
                    self.$message({
                        message: '',
                        type: 'warning',
                    })
                    param.onError()
                })
        },
    },
}
</script>
<style lang="scss">
.product-info {
    padding: 10px 0;
    border-top: 1px solid #eeeeee;
}

.order-code .state-text {
    padding: 2px 4px;
    border-radius: 4px;
    background: #808080;
    color: #ffffff;
}

.order-code .state-text-red {
    background: red;
}

.table-wrap .product-info:first-of-type {
    border-top: none;
}

.table-wrap .el-table__body tbody .el-table__row:nth-child(odd) {
    background: #f5f7fa;
}
</style>



