<template>
    <div class="product-add">
        <!--form-->
        <el-form size="small" ref="form" :model="form" label-width="180px">
            <!---->
            <Basic></Basic>

            <!---->
            <Spec></Spec>

            <!---->
            <Content></Content>

            <!---->
            <Buyset></Buyset>

            <!---->
            <div class="common-button-wrapper">
                <el-button size="small" type="info" @click="cancelFunc"></el-button>
                <el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button>
                <el-button size="small" type="primary" @click="Draft" :loading="loading"></el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
import PorductApi from '@/api/product.js'
import Basic from './part/Basic.vue'
import Spec from './part/Spec.vue'
import Content from './part/Content.vue'
import Buyset from './part/Buyset.vue'
export default {
    components: {
        /**/
        Basic,
        /**/
        Spec,
        /**/
        Content,
        /**/
        Buyset,
    },
    data() {
        return {
            /**/
            loading: false,
            /*form*/
            form: {
                model: {
                    /**/
                    product_name: '',
                    /**/
                    product_no: '',
                    /**/
                    category_id: null,
                    /**/
                    image: [],
                    /**/
                    selling_point: '',
                    /*,1020*/
                    spec_type: 10,
                    /*,2010*/
                    deduct_stock_type: 20,
                    sku: {},
                    /**/
                    spec_many: {
                        /**/
                        spec_attr: [],
                        /**/
                        spec_list: [],
                    },
                    /**/
                    content: '',
                    /*ID*/
                    delivery_id: '',
                    /**/
                    product_status: 40,
                    /**/
                    sales_initial: 0,
                    /*100*/
                    product_sort: 100,
                    /*,10*/
                    is_points_gift: 1,
                    /*,10*/
                    is_points_discount: 1,
                    /**/
                    max_points_discount: 0,
                    /**/
                    is_virtual: 0,
                    /**/
                    limit_num: 0,
                    /**/
                    virtual_auto: 0,
                    /**/
                    virtual_content: '',
                    /**/
                    zone_id: 0,
                },
                /**/
                category: [],
                /**/
                delivery: [],
                /**/
                gradeList: [],
                /**/
                specData: null,
                /**/
                zoneData: [],
            },
        }
    },
    provide: function() {
        return {
            form: this.form,
        }
    },
    created() {
        /**/
        this.getBaseData()
    },
    methods: {
        /**/
        getBaseData: function() {
            let self = this
            PorductApi.getBaseData({}, true)
                .then((res) => {
                    self.loading = false
                    Object.assign(self.form, res.data)
                })
                .catch((error) => {
                    self.loading = false
                })
        },

        /*JSON*/
        convertJson(list) {
            let obj = {}
            list.forEach((item) => {
                obj[item.grade_id] = item.product_equity
            })
            return JSON.stringify(obj)
        },

        /**/
        onSubmit: function() {
            let self = this
            let params = self.form.model
            self.$refs.form.validate((valid) => {
                if (valid) {
                    self.loading = true
                    PorductApi.addProduct(
                        {
                            params: JSON.stringify(params),
                        },
                        true
                    )
                        .then((data) => {
                            self.loading = false
                            self.$message({
                                message: '',
                                type: 'success',
                            })
                            self.$router.push('/product/product/index')
                        })
                        .catch((error) => {
                            self.loading = false
                        })
                }
            })
        },

        /**/
        Draft() {
            let self = this
            self.form.model.product_status = 30
            self.onSubmit()
        },

        /**/
        cancelFunc() {
            this.$router.back(-1)
        },
    },
}
</script>

<style lang="scss" scoped>
.basic-setting-content {
}

.product-add {
    padding-bottom: 100px;
}
</style>



