<template>
    <!--
    	wangxw
    	2019-10-26
    	-
    -->
    <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false">
        <el-form size="small" :model="form" :rules="formRules" ref="form">
            <el-form-item label="" :label-width="formLabelWidth">
                <el-select v-model="form.parent_id">
                    <el-option label="" value="0"></el-option>
                    <el-option :value="cat.category_id" :label="cat.name" :key="cat.category_id" v-for="cat in addform.catList"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="name" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" prop="image_id" :label-width="formLabelWidth">
                <el-row>
                    <el-button type="primary" @click="openUpload"></el-button>
                    <div v-if="form.image_id != ''" class="img">
                        <img :src="file_path" width="100" height="100" />
                    </div>
                </el-row>
            </el-form-item>

            <el-form-item label="" prop="sort" :label-width="formLabelWidth">
                <el-input v-model.number="form.sort" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible"> </el-button>
            <el-button type="primary" @click="addUser" :loading="loading"> </el-button>
        </div>
        <!---->
        <Upload v-if="isupload" :isupload="isupload" :type="type" @returnImgs="returnImgsFunc"></Upload>
    </el-dialog>
</template>

<script>
import PorductApi from '@/api/product.js'
import Upload from '@/components/file/Upload'
export default {
    components: {
        Upload,
    },
    data() {
        return {
            form: {
                parent_id: '0',
                name: '',
                sort: 100,
                image_id: '',
            },
            formRules: {
                name: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                ],
                image_id: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                ],
                sort: [
                    {
                        required: true,
                        message: '',
                    },
                    {
                        type: 'number',
                        message: '',
                    },
                ],
            },
            /**/
            formLabelWidth: '120px',
            /**/
            dialogVisible: false,
            loading: false,
            /**/
            isupload: false,
        }
    },
    props: ['open_add', 'addform'],
    created() {
        this.dialogVisible = this.open_add
    },
    methods: {
        /**/
        addUser() {
            let self = this
            let params = self.form
            self.$refs.form.validate((valid) => {
                if (valid) {
                    self.loading = true
                    PorductApi.catAdd(params)
                        .then((data) => {
                            self.loading = false
                            self.$message({
                                message: '',
                                type: 'success',
                            })
                            self.dialogFormVisible(true)
                        })
                        .catch((error) => {
                            self.loading = false
                        })
                }
            })
        },

        /**/
        dialogFormVisible(e) {
            if (e) {
                this.$emit('closeDialog', {
                    type: 'success',
                    openDialog: false,
                })
            } else {
                this.$emit('closeDialog', {
                    type: 'error',
                    openDialog: false,
                })
            }
        },
        /**/
        openUpload(e) {
            this.type = e
            this.isupload = true
        },
        /**/
        returnImgsFunc(e) {
            if (e != null && e.length > 0) {
                this.file_path = e[0].file_path
                this.form.image_id = e[0].file_id
            }
            this.isupload = false
        },
    },
}
</script>

<style>
.img {
    margin-top: 10px;
}
</style>



