<template>
    <!--
    	luoyiming
    	2019-10-26
    	-
    -->
    <div class="product-add" v-loading="loading">
        <!--form-->
        <el-form size="small" ref="form" :model="form" label-width="180px" v-if="!loading">
            <!---->
            <Basic></Basic>
            <!---->
            <Spec></Spec>

            <!---->
            <Content></Content>

            <!---->
            <Buyset></Buyset>

            <!---->
            <div class="common-button-wrapper">
                <el-button size="small" type="info" @click="cancelFunc"></el-button>

                <template v-if="form.model.product_status == 0">
                    <el-button size="small" type="primary" @click="onSubmit" :disabled="save_loading"></el-button>
                </template>
                <template v-if="form.model.product_status == 30">
                    <el-button size="small" type="primary" @click="audit" :disabled="save_loading"></el-button>
                </template>
                <template v-if="form.model.product_status == 10 || form.model.product_status == 20">
                    <el-button size="small" type="primary" @click="onSubmit" :disabled="save_loading"></el-button>
                </template>
                <template v-if="form.model.product_status == 40">
                    <el-button size="small" type="primary" :disabled="true"></el-button>
                </template>
                <el-button
                    size="small"
                    type="primary"
                    @click="Draft"
                    :loading="loading"
                    v-if="form.model.product_status == 30 || form.model.product_status == 0"
                    ></el-button
                >
            </div>
        </el-form>
    </div>
</template>

<script>
import PorductApi from '@/api/product.js'
import Basic from './part/Basic.vue'
import Spec from './part/Spec.vue'
import Content from './part/Content.vue'
import Buyset from './part/Buyset.vue'
import { formatModel } from '@/utils/base.js'
import select_region from '@/utils/select.region.js'
export default {
    components: {
        /**/
        Basic,
        /**/
        Spec,
        /**/
        Content,
        /**/
        Buyset,
    },
    data() {
        return {
            /*ID*/
            product_id: 0,
            /**/
            scene: 'edit',
            /**/
            loading: true,
            /**/
            save_loading: false,
            /*form*/
            form: {
                model: {},
                /**/
                catgory: [],
                /**/
                delivery: [],
                /**/
                gradeList: [],
                /**/
                specData: null,
                /**/
                zoneData: [],
            },
            /**/
            model: {
                province_id: '',
                city_id: '',
                province_name: '',
                city_name: '',
                /**/
                product_name: '',
                /**/
                category_id: null,
                /**/
                image: [],
                /**/
                selling_point: '',
                /*,1020*/
                spec_type: 10,
                /*,2010*/
                deduct_stock_type: 20,
                /**/
                sku: {
                    /**/
                    product_price: 0,
                    /**/
                    line_price: 0,
                    /**/
                    stock_num: 0,
                    /**/
                    product_weight: 0,
                    /**/
                    product_no: '',
                    /**/
                    love_price: 0,
                    /**/
                    beans_price: 0,
                },
                /**/
                spec_many: {
                    /**/
                    spec_attr: [],
                    /**/
                    spec_list: [],
                },
                /**/
                content: '',
                /*ID*/
                delivery_id: '',
                /**/
                product_status: 10,
                /**/
                sales_initial: 0,
                /*100*/
                product_sort: 100,
                /*,10*/
                is_points_gift: 1,
                /*,10*/
                is_points_discount: 1,
                /**/
                max_points_discount: 0,
                /**/
                is_virtual: 0,
                /**/
                limit_num: 0,
                /**/
                virtual_auto: 0,
                /**/
                virtual_content: '',
                /**/
                zone_id: 0,
            },
            /**/
            areaList: select_region,
        }
    },
    provide: function() {
        return {
            form: this.form,
        }
    },
    created() {
        /**/
        this.product_id = this.$route.query.product_id
        this.scene = this.$route.query.scene
        this.getData()
    },
    methods: {
        chioce(item) {
            this.form.model.province_name = item.name
        },
        chioce1(item1) {
            this.form.model.city_name = item1.name
        },
        /*id*/
        initCity() {
            this.form.model.city_id = ''
        },

        /*id*/
        initRegion() {
            this.form.model.region_id = ''
        },
        /**
         * 
         */
        getData: function() {
            let self = this
            PorductApi.getEditData(
                {
                    product_id: self.product_id,
                    scene: self.scene,
                },
                true
            )
                .then((res) => {
                    self.loading = false
                    self.form.scene = self.scene
                    Object.assign(self.form, res.data)

                    if (res.data.specData) {
                        self.$set(self.form.model, 'spec_many', res.data.specData)
                    } else {
                        self.$set(self.form.model, 'spec_many', {
                            spec_attr: [],
                            spec_list: [],
                        })
                        self.form.model.sku = res.data.model.sku[0]
                    }
                    self.form.model.product_status = res.data.model.product_status.value
                    if (self.scene == 'copy') {
                        self.form.model.product_status = 0
                    }
                })
                .catch((error) => {
                    self.loading = false
                })
        },

        /*JSON*/
        convertJson(list) {
            let obj = {}
            list.forEach((item) => {
                obj[item.grade_id] = item.product_equity
            })
            return JSON.stringify(obj)
        },

        /**/
        onSubmit: function() {
            let self = this
            self.$refs.form.validate((valid) => {
                if (valid) {
                    let params = formatModel(self.model, self.form.model)
                    params.scene = self.scene
                    params.image = self.ImgKeepId(params.image)
                    params.product_id = self.product_id
                    self.save_loading = true
                    PorductApi.editProduct(
                        {
                            product_id: self.product_id,
                            scene: self.scene,
                            params: JSON.stringify(params),
                        },
                        true
                    )
                        .then((data) => {
                            self.save_loading = false
                            self.$message({
                                message: '',
                                type: 'success',
                            })
                            self.cancelFunc()
                        })
                        .catch((error) => {
                            self.save_loading = false
                        })
                }
            })
        },

        /*id*/
        ImgKeepId(list) {
            let arr = []
            for (let i = 0, length = list.length; i < length; i++) {
                let obj = {
                    image_id: list[i].image_id,
                    file_id: list[i].file_id,
                }
                arr.push(obj)
            }
            return arr
        },

        /**/
        Draft() {
            let self = this
            self.form.model.product_status = 30
            self.onSubmit()
        },

        /**/
        audit() {
            let self = this
            self.form.model.product_status = 40
            self.onSubmit()
        },

        /**/
        cancelFunc() {
            this.$router.back(-1)
        },
    },
}
</script>

<style lang="scss" scoped>
.basic-setting-content {
}

.product-add {
    padding-bottom: 50px;
}
</style>



