<template>
  <!--
      	luoyiming
      	2019-06-04
      	---
      -->
  <div v-loading="loading">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="basic"></el-tab-pane>
        <el-tab-pane label="" name="product"></el-tab-pane>
      </el-tabs>
      <template v-if="activeName == 'basic'">
        <el-form-item label="" prop="name" :rules="[{ required: true, message: ' ' }]">
          <el-input v-model="form.name" placeholder=""></el-input>
          <div class="tips">10010</div>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.color">
            <el-radio :label="10"></el-radio>
            <el-radio :label="20"></el-radio>
            <el-radio :label="30"></el-radio>
            <el-radio :label="40"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.coupon_type">
            <el-radio :label="10"></el-radio>
            <el-radio :label="20"></el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-if="form.coupon_type == 10">
          <el-form-item label="">
            <el-input v-model="form.reduce_price" placeholder="" type="number"></el-input>
          </el-form-item>
        </div>
        <div v-else>
          <el-form-item label=" ">
            <el-input v-model="form.discount" placeholder="" type="number"></el-input>
            <div class="tips">0-109.59.50</div>
          </el-form-item>
        </div>

        <el-form-item label="">
          <el-input v-model="form.min_price" placeholder="" type="number"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.expire_type">
            <el-radio :label="10"></el-radio>
            <el-radio :label="20"></el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-if="form.expire_type == 10">
          <el-form-item label="">
            <el-input v-model="form.expire_day" placeholder="" type="number"></el-input>
          </el-form-item>
        </div>
        <div v-else>
          <el-form-item label="">
            <el-date-picker v-model="form.active_time" type="daterange" align="right" unlink-panels
              value-format="yyyy-MM-dd" range-separator="" start-placeholder="" end-placeholder=""
              :picker-options="pickerOptions0"></el-date-picker>
          </el-form-item>
        </div>
        <el-form-item label=" " prop="total_num" :rules="[{ required: true, message: ' ' }]">
          <el-input v-model="form.total_num" placeholder="" type="number"></el-input>
          <div class="tips">-1</div>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.free_limit">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
          </el-radio-group>
          <div class="tips"></div>
        </el-form-item>
        <el-form-item label="">
          <el-input type="number" v-model="form.sort" placeholder=""></el-input>
        </el-form-item>
      </template>
      <template v-if="activeName == 'product'">
        <el-form-item label="">
          <el-radio-group v-model="form.apply_range">
            <el-radio :label="10"></el-radio>
            <el-radio :label="20"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" v-if="form.apply_range == 20">
          <el-button type="primary" @click="addProduct()"></el-button>
          <el-table :data="prodcutData" style="width: 40%">
            <el-table-column prop="product_name" label="">
            </el-table-column>
            <el-table-column prop="product_" label="">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click='delProduct(scope.row)'>  </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
      </template>
      <!---->
      <div class="common-button-wrapper">
        <el-button type="info" size="small" @click="cancelFunc" :loading="loading"></el-button>
        <el-button type="primary" size="small" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
    <!---->
    <Product :isproduct="isproduct" :excludeIds="exclude_ids" :islist="true" @closeDialog="closeProductFunc"></Product>
  </div>
</template>

<script>
  import Product from '@/components/product/Product.vue';
  import CouponApi from '@/api/coupon.js';
  import {
    formatModel
  } from '@/utils/base.js'
  export default {
    components: {
      Product
    },
    data() {
      return {
        activeName: 'basic',
        /*form*/
        form: {
          /*ID*/
          coupon_id: '',
          /**/
          name: '',
          /**/
          color: '',
          /**/
          coupon_type: '',
          /**/
          reduce_price: '',
          /**/
          discount: '',
          /**/
          min_price: '',
          /**/
          expire_type: '',
          /**/
          expire_day: '',
          /**/
          active_time: '',
          /**/
          total_num: '',
          /**/
          sort: 1,
          /*0123*/
          free_limit: 0,
          apply_range: 10,
          product: [],
        },
        list: {},
        /**/
        loading: true,
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; //-8.64e7
          }
        },
        /**/
        category: [],
        /*id*/
        exclude_ids: [],
        prodcutData: [],
        isproduct: false
      };
    },
    created() {
      let coupon_id = this.$route.query.coupon_id;
      this.getData(coupon_id);
    },

    methods: {
      getData(coupon_id) {
        let self = this;
        CouponApi.couponDetail({
              coupon_id: coupon_id
            },
            true
          )
          .then(res => {
            let obj = {};
            obj = res.data.detail;
            //
            obj.color = res.data.detail.color.value;
            obj.coupon_type = res.data.detail.coupon_type.value;
            if (obj.apply_range == 10) {
              self.form.product = [];
              self.prodcutData = [];
            } else {
              self.prodcutData = res.data.detail.product_list;
              self.exclude_ids = res.data.detail.product;
            }
            self.form = formatModel(self.form, obj);
            self.loading = false;
            console.log(self.exclude_ids);
          })
          .catch(error => {
            self.loading = false;
          });
      },

      /**/
      onSubmit() {
        let self = this;
        let form = self.form;
        self.$refs.form.validate(valid => {
          if (valid) {
            self.loading = true;
            CouponApi.editCoupon(form, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/coupon/coupon/index');
              })
              .catch(error => {
                self.loading = false;
              });
          } else {
            self.loading = false;
          }
        });
      },

      /**/
      cancelFunc() {
        this.$router.push({
          path: '/coupon/coupon/index',
        });
      },

      /**/
      addProduct() {
        this.isproduct = true;
      },
      /**/
      closeProductFunc(e) {
        let self = this;
        self.isproduct = e.openDialog;
        if (e.type == 'success') {
          e.params.forEach((item)=>{
            console.log(item);
            self.form.product.push(item.product_id);
            self.prodcutData.push(item);
          });
        }
      },
      delProduct(item) {
        let self = this;
        let n = self.prodcutData.indexOf(item);
        self.prodcutData.splice(n, 1);
        self.form.product.splice(n, 1);
      },
    }
  };
</script>

<style lang="scss" scoped>
  .product-add {
    padding-bottom: 50px;
  }

  .tips {
    color: #ccc;
  }
</style>



