<template>
    <div>
        <div class="common-form"></div>
        <div class="basic-setting-content">
            <el-form-item label="">
                <el-date-picker v-model="form.model.day" type="date" value-format="yyyy-MM-dd" placeholder="" @change="controllStartTime">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-time-picker
                    v-model="form.model.starttime"
                    format="HH:mm"
                    placeholder=""
                    value-format="HH:mm"
                    @change="controllEndTime"
                ></el-time-picker>
                -
                <el-time-picker v-model="form.model.endtime" format="HH:mm" placeholder="" value-format="HH:mm"></el-time-picker>
            </el-form-item>
            <el-form-item label="" prop="resource">
                <el-radio-group v-model="form.model.is_new">
                    <el-radio :label="0"></el-radio>
                    <el-radio :label="1"></el-radio>
                </el-radio-group>
            </el-form-item>
        </div>
    </div>
</template>

<script>
export default {
    inject: ['form'],
    data() {
        return {
            changeStartTime: new Date(),
            changeEndTime: new Date(),
        }
    },

    created() {
        this.form.model.day = this.formatDate(new Date(), 'yyyy-MM-dd')
    },

    methods: {
        controllEndTime() {
            if (this.form.model.starttime !== '' && this.form.model.starttime !== null) {
                // ;
                this.changeEndTime = new Date(new Date(this.form.model.starttime.getTime() + 30 * 60 * 1000)) // 30
                this.form.model.endtime = this.changeEndTime
            }
        },
        controllStartTime() {
            this.handleTime()
        },
        handleTime() {
            var tempTime = new Date()
            const toStringTempTime = this.formatDate(tempTime, 'yyyy-MM-dd') // --
            //const formSelectedTime = this.formatDate(this.form.model.day, 'yyyy-MM-dd') // ,
            const formSelectedTime = this.form.model.day // ,
            console.log(formSelectedTime)
            if (formSelectedTime === toStringTempTime) {
                // 
                this.changeStartTime = new Date()
                this.form.model.starttime = this.changeStartTime // 
            } else {
                this.changeStartTime = new Date(new Date(new Date().toLocaleDateString()).getTime() - 24 * 60 * 60 * 1000) // 0
                this.form.model.starttime = this.changeStartTime // 0
            }
            this.controllEndTime() // 
        },
        formatDate(date, type) {
            var now = date
            var newTime
            if (type === 'yyyy-MM-dd') {
                var year = now.getFullYear()
                var month = now.getMonth() + 1
                month = month < 10 ? '0' + month : month
                var day = now.getDate()
                day = day < 10 ? '0' + day : day
                newTime = year + '-' + month + '-' + day
            }
            if (type === 'HH:ss') {
                var getHour = now.getHours()
                getHour = getHour < 10 ? '0' + getHour : getHour
                var minutes = now.getMinutes()
                minutes = minutes < 10 ? '0' + minutes : minutes
                newTime = getHour + ':' + minutes
            }
            return newTime
        },
    },
}
</script>
<style></style>



